@font-face {
  font-family: "Noe Display";
  src: url('~fonts/NoeDisplay-Bold.woff2') format('font-woff2'),
    url('~fonts/NoeDisplay-Bold.woff') format('font-woff'),
    url('~fonts/NoeDisplay-Bold.ttf') format('truetype');
